import React, { useState } from "react";
import { Form, Input, Select, InputNumber, Button } from "antd";
import { mnemonicToWallet } from "../utils/mnemonic";
import { MnemonicSchema } from "../models/validate";

const MnemonicInput: React.FC = (props: any) => {
    const [mnemonic, setMnemonic] = useState("");
    const [isMnemonicValid, setIsMnemonicValid] = useState("");
    const layout = {
        labelCol: { span: 8 },
        wrapperCol: { span: 16 },
    };

    const onFinish = (values: any) => {
        console.log(values);
    };
    const handleMnemonicInput = (evt: any) => {
        const v = MnemonicSchema.validate(evt.target.value, { abortEarly: false });
        if (v.error) {
            setIsMnemonicValid("error");
        } else {
            setIsMnemonicValid("success");
            setMnemonic(evt.target.value);
        }
    };
    const handleGetWallet = () => {
        const wallet = mnemonicToWallet(mnemonic);
        console.log(wallet.privateKey);
        props.addData(wallet.privateKey);
    };
    return (
        <Form {...layout} name="nest-messages" onFinish={onFinish} style={{ maxWidth: 600 }}>
            <Form.Item name="mnemonic" label="Set Mnemonic">
                <Input.TextArea onChange={handleMnemonicInput} status={isMnemonicValid as any} />
            </Form.Item>
            <Form.Item label=" " colon={false}>
                <Button type="primary" htmlType="button" onClick={handleGetWallet}>
                    Get Private Key
                </Button>
            </Form.Item>
            <Form.Item label=" " colon={false}>
                {}
            </Form.Item>
            <Form.Item label="Select Project">
                <Select>
                    <Select.Option value="projectId">Project</Select.Option>
                </Select>
            </Form.Item>
            <Form.Item label="Address Number">
                <InputNumber />
            </Form.Item>
            <Form.Item label="Select Operation">
                <Select>
                    <Select.Option value="operationId">Operation</Select.Option>
                </Select>
            </Form.Item>
        </Form>
    );
};

export default MnemonicInput;
